@use "styles" as *;
.acomplishments {
  margin-top: 5%;
  padding: 30px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background: #ffffff;
  div.heading {
    display: flex;
    justify-content: space-between;
    small {
      color: #a2a2a2;
      font-weight: 600;
    }
  }
  h4 {
    font-weight: bold;
    margin-bottom: 3%;
  }
  small {
    color: 001243;
    font-weight: 600;
  }
}
.acomplishments .form-row {
  display: flex;
  justify-content: space-between;
  width: 100%;
  @include maxQ($tablet) {
    //flex-direction: column;
  }
  
}
.acomplishments .form-group-col {
  width: 100%;
  .form-group {
    width: 80%;
  }
}
.acomplishments .form-group {
  width: 40%;

  input {
    width: 100%;
  }
  @include maxQ($tablet) {
    width: 70%;
    input {
      width: 100%;
    }
  }
}
